import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import Main from '../componentes/Main';
import Modal from '../componentes/Modal';
import useModal from '../hooks/useModal';
import { Link } from 'react-router-dom';
import Loading from '../componentes/Loading';
import {useAlumno} from '../Context/alumnoContext';

export default function Cursos(){
    const [cursos,setCursos] = useState([])
    const [cursosAmostrar,setCursosAmostrar]=useState([])
    const {toggle, isShowing } = useModal();
    const [materias, setMaterias ] = useState([]);
    const [profesores, setProfesores ] = useState([]);
    const [tipos, setTipos ] = useState([]);
    const [dias, setDias ] = useState([]);
    const [criterio, setCriterio ] = useState('original');
    const [dia,setDia]=useState(-1);
    const [tipo,setTipo]=useState(-1);
    const [profesor,setProfesor]=useState(-1);
    const [materia,setMateria]=useState(-1);
    const [tipoCurso,setTipoCurso]=useState(-1); // 0 Regular 1 Recuperatorio
    const [cursosRecuperatorios,setCursosRecuperatorios]= useState(-1);
    // para activar el modal llamar a la función toggle en con alguna condicion o un evento...
    const [cargandoCursos,setCargandoCursos] = useState(false);
    const {cuatrimestreActivo} = useAlumno();
   // const {alumno, cambiarAlumno} = useAlumno();

    useEffect(()=>{

    const buscarCursos = async ()=>{

        setCargandoCursos(true)
        try{          
            const {data} = await Axios.get(`/api/cursos/all/${cuatrimestreActivo.id_cuatrimestre}`)
            setCursos(data)
            setCargandoCursos(false)
        }catch(err){
            console.log(err)
            setCargandoCursos(false)
        }
    }
        
        buscarCursos()
    },[cuatrimestreActivo])

    useEffect(()=>{
            const materias = materiasDeLosCursos()
            setMaterias(materias)
            const profesores = profesoresDeLosCursos()
            setProfesores(profesores)
            const dias = diasDeLosCursos()
            setDias(dias)
            const tipos = tiposDeLosCursos()
            setTipos(tipos)

            setCursosAmostrar(cursos)
    },[cursos])

  /*  return <>
        <Modal hide={toggle} isShowing={isShowing}>
            <h1>SOY UN MODAL</h1>
        </Modal>
    </>
*/

const materiasDeLosCursos = ()=>{

    return cursos.map(item=>item.descripcion).sort().filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const profesoresDeLosCursos = ()=>{

    return cursos.map(item=>item.nombre).sort().filter((item,index,vector)=>
        item != vector[index-1])
   
}

const diasDeLosCursos = ()=>{
    // aquì ordeno al array por id de dia en lugar de ordenarlo alfabeticamente si no Jueves vendría primero que Lunes
    return cursos.sort((a,b)=>a.dia - b.dia).map(item=>item.DiaHora).filter((item,index,vector)=>
        item != vector[index-1] )
   
}

const tiposDeLosCursos = ()=>{

    return ['I','G']
   
}

const handleChangeSelectProfesores = (e)=> {
    
    if (e.target.value === "-1" ){
        return 
    }

    setProfesor(e.target.value)
    setTipo(-1);
    setDia(-1);
    setMateria(-1);
    setCursosRecuperatorios(-1);
    setTipoCurso(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.nombre===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const handleChangeSelectMaterias = (e)=> {

    if (e.target.value === "-1" ){
        return 
    }

    setMateria(e.target.value)
    setProfesor(-1)
    setTipo(-1);
    setDia(-1);
    setCursosRecuperatorios(-1);
    setTipoCurso(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.descripcion===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)


}

const handleChangeSelectDias = (e)=> {

    if (e.target.value === "-1" ){
        return 
    }

    setDia(e.target.value);
    setTipo(-1);
    setProfesor(-1);
    setMateria(-1);
    setCursosRecuperatorios(-1);
    setTipoCurso(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.DiaHora===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)

}

const handleChangeSelectTipos = (e)=> {

    if (e.target.value === "-1" ){
        return 
    }

    setDia(-1);
    setTipo(-1);
    setProfesor(-1);
    setMateria(-1);
    setCursosRecuperatorios(-1);
    setTipoCurso(e.target.value);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.tipo===e.target.value)
    setCursosAmostrar(filtrarVectorCursosOriginal)

}

const handleChangeSelectME = (e)=> {

    if (e.target.value === "-1" ){
        return 
    }

    const valorBooleano = e.target.value==="true" ? true: false;

    setCursosRecuperatorios(valorBooleano);
    setTipo(-1);
    setProfesor(-1);
    setMateria(-1);
    setTipoCurso(-1);
    setDia(-1);

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.alerta===valorBooleano)
    setCursosAmostrar(filtrarVectorCursosOriginal)

}

const cambiarTipoCurso = (e)=>{
    // viene Standard, Ensamble o Instrumental
    setTipoCurso(e.target.value)
}


const cambiarCursosRecuperatorios = (e)=>{
    // viene 1 o 0 para indicar si es o no recuperatorio 
    setCursosRecuperatorios(e.target.value)
}

const handleChangeSelectTipo = (e)=> {

    if (e.target.value === "-1" ){
        return 
    }

    setTipo(e.target.value);
    setDia(-1);
    setProfesor(-1);
    setMateria(-1);

    const tipoAbuscar =  e.target.value ==="I" ? 0 : 1;

    const filtrarVectorCursosOriginal = cursos.filter(item=>item.grupal===tipoAbuscar)
    setCursosAmostrar(filtrarVectorCursosOriginal)

}

if (cargandoCursos){
    return <Main center><Loading/></Main>
  };

  //`/curso/${curso.nro_curso}`
return(
    <Main>
        {cuatrimestreActivo && <Cabecera cuatrimestreActivo={cuatrimestreActivo}/>}
        <div className="bg-blue text-white p-4 rounded relative"> 
        <TipoCursos cambiarTipoCurso={handleChangeSelectTipos} 
                    cambiarCursosRecuperatorios={handleChangeSelectME}
                    tipoCurso={tipoCurso} cursoRecuperatorio={cursosRecuperatorios}/>
        <span className="resultados absolute">{cursosAmostrar.length} cursos encontrados</span>                    
        <table className="table mt-2">
            <thead className="bg-blue-500 text-white ">
                <tr>
                    <td scope="col">#ID</td>
                    <td scope="col"></td>
                    <td scope="col">Materia</td>
                    <td scope="col">Profesor</td>
                    <td scope="col">Día</td>
                    <td scope="col">Hora</td>
                    <td title="Cantidad de inscriptos en el curso" scope="col">Insc</td>
                    <td title="Cantidad de lugares disponibles en el curso " scope="col">Disp</td>
                    <td title="Curso Regular o Mesa de examen" className="tipo-curso" scope="col">ME</td>
                    <td title="Curso Standard, Instrumental o Ensamble" className="tipo-curso" scope="col">Tipo</td>
                </tr>
            </thead>
            <tbody>
            <tr>
                    <td scope="col"></td>
                    {/*<td scope="col"><Seleccionador valor={tipo} onchange={handleChangeSelectTipo} vector = {tipos}/></td>*/}
                    <td scope="col"></td>
                    <td scope="col"><Seleccionador valor={materia} onchange={handleChangeSelectMaterias} vector = {materias}/></td>
                    <td scope="col"><Seleccionador valor={profesor} onchange={handleChangeSelectProfesores} vector = {profesores}/></td>
                    <td scope="col"><Seleccionador valor={dia} onchange={handleChangeSelectDias} vector = {dias}/></td>
                    <td scope="col"></td>
                    <td scope="col"></td>
                    <td scope="col"></td>
                </tr>
            {
                cursosAmostrar.map(curso => {
                return (
                    <tr key={curso.nro_curso} className="bg-blueTabla">
                        <td>{curso.nro_curso}</td>
                        <td>{curso.grupal ? 'G' : 'I'}</td>
                        <td>
                            <Link 
                                className="text-white" 
                                to={{
                                    pathname: `/curso/${curso.nro_curso}`,
                                    state: {...curso}
                                }} > 
                                {curso.descripcion}
                            </Link> 
                        </td>
                        <td>{curso.nombre}</td>
                        <td>{curso.DiaHora}</td>
                        <td>{curso.comienzo} hs</td>
                        <td>{curso.nota}</td>
                        <td className={curso.disponibilidad>0 ? 'dispo-1' : 'dispo-0'}>{curso.disponibilidad}</td>        
                        <td title={curso.alerta===false ? 'Regular' : 'Mesa de examen'} className="tipo-curso">{curso.alerta===false ? 'R' : 'ME'}</td>
                        <td title={curso.tipo} className="tipo-curso">{curso.tipo}</td>
                    </tr>
                   )
                })
            }
            </tbody>
        </table>
      </div>
    </Main>
)
    }

function Seleccionador({vector,onchange,valor}){
    return (            
        <div className="input-field col s12">
            <select value={valor} onChange = {onchange} className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                <option value="-1" key="-1">Seleccionar</option>
                {vector.map(item=><option value={item} key={item}>{item}</option> )}
            </select>
        </div>
        )
}    

function Cabecera({cuatrimestreActivo}){
    return <span className="cabecera">{`Listado de cursos ${cuatrimestreActivo.nombre}`}</span>
}

function TipoCursos({cambiarTipoCurso,cambiarCursosRecuperatorios, tipoCurso, cursoRecuperatorio}){
    return (
        <div className="flex f-row selecTipoCurso absolute">

            <span title="Curso Regular o Mesa de examen" className="tipo-curso mr-4 ml-4">R/ME</span>

            <select title="Curso Regular o Mesa de examen" value={cursoRecuperatorio} 
                className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline" name="" id="" 
                onChange={cambiarCursosRecuperatorios}>
                <option value="-1">Seleccionar</option>
                <option value="false">Regulares</option>
                <option value="true">Recuperatorios</option>
            </select>

            <span title="Curso Standard, Instrumental o Ensamble" className="tipo-curso mr-4 ml-4">Tipo</span>

            <select title="Curso Standard, Instrumental o Ensamble" value={tipoCurso} 
                className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline" name="" id="" 
                onChange={cambiarTipoCurso}>
                <option value="-1">Seleccionar</option>
                <option value="Standard">Standard</option>
                <option value="Instrumental">Instrumental</option>
                <option value="Ensamble">Ensamble</option>
            </select>

        </div>

    )
    
}

