import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhone, faAngleDoubleRight, faAngleDoubleLeft, faPhoneSquare, faMobile, faEnvelopeSquare, faEnvelopeOpenText, faSearchPlus } from '@fortawesome/free-solid-svg-icons';
import { faWindowClose, faUser, faPlusSquare, faMinusSquare, faWindowMaximize, faFilePdf  } from '@fortawesome/free-regular-svg-icons';
import Loading from '../componentes/Loading';
import Modal from '../componentes/Modal';
import useModal from '../hooks/useModal';
import { v4 as uuidv4 } from 'uuid';
import ConstanciaAL from './ConstanciaAL';
import Analitico from './Analitico';
import {imprimir as imprimirComprobanteInsc} from '../impresiones/comprobante-insc'
import {imprimir as imprimirFicha} from '../impresiones/ficha-al'
import Swal from 'sweetalert2';
import FichaCurso from './FichaCurso'
import FormularioContactoAlumno from '../formularios/FormularioContactoAlumno';
import {useAlumno} from '../Context/alumnoContext'

export default function DatosAlumno({id}){

const [historial,setHistorial]=useState([])
const [instrumentos,setInstrumentos]=useState([])
const [materiasAprobadasTest,setMateriasAprobadasTest]=useState([])
const [datosDelAlumno,setDatosDelAlumno]=useState([])
const [cargandoDatos,setCargandoDatos]=useState(true)
const {toggle, isShowing} = useModal();
const [mostrarHistorial, setMostrarHistorial] = useState(false);
const [mostrarMasDatos, setMostrarMasDatos] = useState(false);
const [ampliarCursadas,setAmpliarCursadas] = useState(false);
const [mostrarLateralmente,setMostrarLateralmente] = useState(false);
const [abrirConstancia,setAbrirConstancia]=useState(false);
const [abrirAnalitico,setAbrirAnalitico]=useState(false);
const {cuatrimestreActivo} = useAlumno();

useEffect(()=>{
    const buscarDatosAlumno = async ()=>{
        // a promise.all le paso un array de promesas
        try{
            const vectorRetorno = await Promise.all([Axios.get(`/api/alumnos/historial/${id}/1`),
            Axios.get(`/api/alumnos/instrumentos/${id}`),
            Axios.get(`/api/alumnos/${id}`),
            Axios.get(`/api/alumnos/materiastest/${id}`)])
            setCargandoDatos(false)
            // promise.all me devuelve un array con el resultado de las promesas en el mismo orden                                            
            setHistorial(vectorRetorno[0].data);
            setInstrumentos(vectorRetorno[1].data);
            setDatosDelAlumno(...vectorRetorno[2].data); // copio directamente el objeto
            setMateriasAprobadasTest(vectorRetorno[3].data); // copio directamente el objeto


        }catch(err){

            console.log(err)
            const mensaje_html = `<p>La busqueda del alumno falló</p><p>${err}</p>`

            Swal.fire({
                html:mensaje_html,
                icon: 'warning',
                confirmButtonColor: '#3085d6',
              })
        }

    }

    buscarDatosAlumno()
},[id])


useEffect(()=>{
    console.log(historial)
    console.log(instrumentos)
    console.log(datosDelAlumno)
},[historial,instrumentos,datosDelAlumno,materiasAprobadasTest])

useEffect(()=>{
    if (!isShowing){
        if (mostrarHistorial){  // si se cierra porque muestra el historial 
            setMostrarHistorial(false)
        }
        if (mostrarMasDatos){ // si se cierra porque muestra otros datos 
            setMostrarMasDatos(false)
        }   
        if (abrirConstancia){ // si se cierra porque muestra otros datos 
            setAbrirConstancia(false)
        }   
        if (abrirAnalitico){ // si se cierra porque muestra otros datos 
            setAbrirAnalitico(false)
        }            
    }
},[isShowing]) // proceso algo cada vez que el modal se cierra

const handleSubmit=(e)=>{
    e.preventDefault()
}

const ejecutarImprimirComprobanteInsc = ()=>{
//    generarConstancia(fecha,periodo,alumno,cursadas)
const objetoAlumno = {alumno:`${datosDelAlumno.nombre} ${datosDelAlumno.apellido}`, 
                  id_alumno:datosDelAlumno.id_alumno, 
                  documento:datosDelAlumno.documento}

    imprimirComprobanteInsc(cuatrimestreActivo.nombre,objetoAlumno,historial,cuatrimestreActivo.nombre)
}

const ejecutarImprimirFicha = ()=>{
    //    generarConstancia(fecha,periodo,alumno,cursadas)
    const alumno36 = {alumno:"juan perez", id_alumno:555, documento:"23865621",fecha_nac:"27/03/1974", instrumentos:"piano",fecha_insc:"25/05/2000"}
    
        imprimirFicha(materiasAprobadasTest,instrumentos,datosDelAlumno)
    }

    if (cargandoDatos){
        return <Loading/>
    }

    return (
        <div className="p-4 rounded">
            <button title={mostrarLateralmente ? 'Visualizar los datos del alumno horizontalmente en el centro':'Visualizar los datos del alumno verticalmente a la derecha'} className="boton-lateral" onClick={()=>mostrarLateralmente ? setMostrarLateralmente(false) : setMostrarLateralmente(true)}>
                <FontAwesomeIcon icon={mostrarLateralmente ? faAngleDoubleLeft : faAngleDoubleRight}/>
            </button>
            { !mostrarLateralmente && <Impresiones 
                    modoColumna = {false}
                    toggle={toggle} 
                    setAbrirConstancia={setAbrirConstancia} 
                    setAbrirAnalitico={setAbrirAnalitico} 
                    ejecutarImprimirComprobanteInsc={ejecutarImprimirComprobanteInsc} 
                    ejecutarImprimirFicha={ejecutarImprimirFicha}/>
            }
            <div className= {mostrarLateralmente ? "flex f-col da-lateral" : "flex f-row"} > 
            { mostrarLateralmente && <Impresiones 
                    modoColumna = {true}
                    toggle={toggle} 
                    setAbrirConstancia={setAbrirConstancia} 
                    setAbrirAnalitico={setAbrirAnalitico} 
                    ejecutarImprimirComprobanteInsc={ejecutarImprimirComprobanteInsc} 
                    ejecutarImprimirFicha={ejecutarImprimirFicha}/>
            }
                <div className={ampliarCursadas ? "max-w-sm rounded overflow-hidden mb-6 ml-4 minw400" : "max-w-sm rounded overflow-hidden mb-6 ml-4"}>
                    <div className="px-6 py-4 titulosAlumno">
                        <div className="mb-2">Cursadas actuales ({historial.length})</div>
                        <p className="text-gray-700 text-base">
                            
                        </p>
                    </div>  
                    {!ampliarCursadas && <div className="px-6 py-4 flex flex-wrap">
                        {historial.map(
                            item=><span title={crearTitulo(item)} key={item.nro_curso} className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">
                            {item.mensaje}        
                        </span>
                        )}
                        
                    </div>}
                    { ampliarCursadas && <div className="px-6 py-4">
                        {historial.map(
                            item=><div key={item.nro_curso} className="block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">
                            <span className="listaCursadasAmpliada w-30 fw-600">{item.mensaje}</span> 
                            <span className="listaCursadasAmpliada w-100">{item.descripcion}</span> 
                            <span className="listaCursadasAmpliada w-100">{item.profesor} </span> 
                            <span className="listaCursadasAmpliada w-100">{item.DiaHora} </span>       
                            <span className="listaCursadasAmpliada w-100">Aula: {item.Aula} </span>       
                        </div>
                        )}
                         <button title="Cerrar el detalle de cursadas actuales" onClick={()=>{setAmpliarCursadas(false)}}>
                             <FontAwesomeIcon icon={faMinusSquare}/>
                        </button>  
                      {/*  <button onClick={()=>{setMostrarHistorial(true); toggle()}}>
                             <FontAwesomeIcon icon={faSearchPlus}/>
                        </button> */}                     
                    </div>}
                    {!ampliarCursadas && <button title="Ver el detalle de las cursadas actuales" onClick={()=>{setAmpliarCursadas(true)}}>
                            <FontAwesomeIcon icon={faPlusSquare}/>
                        </button>
                     }   
  
                        {mostrarHistorial && <HistorialModal hide={toggle} isShowing={isShowing} historial={historial}/>} 
                      {/*  <button onClick={()=>{setMostrarMasDatos(true); toggle()}}>Ampliar 2 </button> */ }
                        {mostrarMasDatos && <InformacionModal hide={toggle} isShowing={isShowing} historial={historial}/>}                         
                        {abrirConstancia && <ConstanciaModal hide={toggle} isShowing={isShowing} alumno={datosDelAlumno} /> }
                        {abrirAnalitico && <AnaliticoModal hide={toggle} isShowing={isShowing} alumno={datosDelAlumno} /> }
                </div>
                   
                <div className="max-w-sm rounded overflow-hidden mb-6 ml-4">
                    <div className="px-6 py-4 titulosAlumno">
                        <div className="mb-2">Instrumentos</div>
                        <p className="text-gray-700 text-base">
                            
                        </p>
                    </div>  
                    <div className="px-6 py-4">
                    <div className="flex flex-wrap">
                        {instrumentos.map(
                            item=><div className="flex f-col" key={item.id_instrumento}>
                            <span className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2    ">
                                {item.instrumentos}        
                            </span>
                            <span className="text-center inline-block bg-gray-200 px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2 border-bottom-solid">
                                <span title="Nivel ensamble">{item.nivel_e} </span><span title="Nivel instrumental">{item.nivel_i}</span>         
                            </span>
                            </div>
                        )}
                        </div>
                    </div>
                </div>
                <div className="max-w-sm rounded overflow-hidden mb-6 ml-4">
                    <div className="px-6 py-4 titulosAlumno">
                        <div className="mb-2">Contacto</div>
                        <p className="text-gray-700 text-base">
                        </p>
                    </div>  
                    <div className="px-6 py-4">
                      <span title="Teléfono" className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2 mr-2">
                                <FontAwesomeIcon icon={faPhone}></FontAwesomeIcon>  {datosDelAlumno.telefono}     
                         </span>
                        <span title="Teléfono alternativo" className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2 mr-2">
                                {datosDelAlumno.Telef_Alternativo}       
                         </span>                        
                        <span title="Teléfono laboral" className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2 mr-2">
                                {datosDelAlumno.Telef_Laboral}     
                        </span>    
                        <span title="Celular" className="whitespace-no-wrap inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2    ">
                                <FontAwesomeIcon icon={faMobile}></FontAwesomeIcon>{datosDelAlumno.Celular}     
                        </span>    
                                                    
                        <p className="bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2    ">
                                <FontAwesomeIcon icon={faEnvelopeOpenText}></FontAwesomeIcon><span title="E-mail principal">{datosDelAlumno.email}</span> <span title="E-mail secundario">{datosDelAlumno.Email_Secundario}</span>      
                        </p>     
                      {/*  <FormularioContactoAlumno handleSubmit={handleSubmit} 
                                telefono={datosDelAlumno.telefono} 
                                celular={datosDelAlumno.Celular}
                                telef_alternativo = {datosDelAlumno.Telef_Alternativo}
                                telef_laboral = {datosDelAlumno.Telef_Laboral}
                                email = {datosDelAlumno.email}
                                email_secundario = {datosDelAlumno.Email_Secundario} 
                        /> */}
                     </div>
                </div>          
                <div className="mw-200 rounded overflow-hidden mb-6 ml-4">
                    <div className="px-6 py-4 titulosAlumno">
                        <div className="mb-2">Observaciones</div>
                        <p className="text-gray-700 text-base">
                            
                        </p>
                    </div>  
                    <div className="px-6 py-4 ">
                      <span className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">
                            {datosDelAlumno.obs_finanzas}        
                      </span>
                 
                        
                    </div>
                </div>                        
            </div>
                        {/*<div>{JSON.stringify(datosDelAlumno)}</div>*/ }    
        </div>
        

    )
}

function HistorialModal({hide,isShowing,historial}){
    return  <Modal hide={hide} isShowing={isShowing}>
                { historial.map(item=>{
                            return(
                                <div key={uuidv4()}>
                                <p className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">{item.descripcion}</p>
                                <p className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">{item.nro_curso}</p>
                                <p className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">{item.profesor}</p>
                                <p className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">{item.DiaHora}</p>
                                <p className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">{item.Aula}</p>
                                <p className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2">{item.columna}</p>
                                </div>
                            )
                        })}
            </Modal>
}

function InformacionModal({hide,isShowing,historial}){
    return  <Modal hide={hide} isShowing={isShowing}>
                <h1>MAS DATOS</h1>
            </Modal>
}

function ConstanciaModal({hide,isShowing,alumno}){
    return  <Modal hide={hide} isShowing={isShowing} titulo="Constancia de alumno regular">
                <ConstanciaAL alumno={alumno}/>
            </Modal>
}

function AnaliticoModal({hide,isShowing,alumno}){
    const estilo = {width:'800px'}
    return  <Modal hide={hide} isShowing={isShowing} titulo="Analítico" estilo={estilo}>
                <Analitico alumno={alumno}/>
            </Modal>
}

function FormularioContacto({handleSubmit,telefono, celular, telef_alternativo,telef_laboral,email,email_secundario}){
    return(

        <div className="Signup">
        <div className="actualizarFichaAlumno flex flex-wrap">
            <form onSubmit={handleSubmit }>
                {/* Tengo conectado el input email con el estado usuario.email a través del atributo value y del evento onChange */}
                <input value={telefono}
                    type="text" 
                    name="username" 
                    placeholder="Teléfono" 
                    className="" required/>
                <input value={telef_alternativo}
                    type="text" 
                    name="username" 
                    placeholder="Teléfono 2" 
                    className="" required/>    
                <input value={telef_laboral}
                    type="text" 
                    name="username" 
                    placeholder="Tel. laboral" 
                    className="" required/>   
                <input value={celular}
                    type="text" 
                    name="username" 
                    placeholder="Celular" 
                    className="" required/>
                <input value={email}
                    type="text" 
                    name="username" 
                    placeholder="email" 
                    className="" required/>   
                <input value={email_secundario}
                    type="text" 
                    name="username" 
                    placeholder="email 2" 
                    className="" required/>                                                           
                <button className="Form__submit" type="submit">Actualizar</button>
            </form>
        </div>
    </div>

    )
}

function crearTitulo(cursadas){
    return `${cursadas.descripcion}
${cursadas.DiaHora}
${cursadas.profesor}
Aula: ${cursadas.Aula}
Fecha de inscripción: ${cursadas.columna.trim()}\n`
}

function Impresiones({modoColumna,toggle,setAbrirConstancia,setAbrirAnalitico,ejecutarImprimirComprobanteInsc,ejecutarImprimirFicha}){
    return <div className={modoColumna ? "" : "flex flex-wrap"}>
    <button title="Imprimir la constancia de alumno regular" onClick={()=>{setAbrirConstancia(true); toggle()}}>
        <FontAwesomeIcon icon={faFilePdf} /> Constancia AR
    </button>
    <button title="Imprimir el analítico" onClick={()=>{setAbrirAnalitico(true); toggle()}}>
        <FontAwesomeIcon icon={faFilePdf} /> Analítico
    </button>
    <button title="Imprimir el comprobante de inscripción de las cursadas actuales" onClick={()=>{ejecutarImprimirComprobanteInsc(true); toggle()}}>
        <FontAwesomeIcon icon={faFilePdf} /> Comprobante Insc.
    </button>
    <button title="Imprimir la ficha del alumno" onClick={()=>{ejecutarImprimirFicha()}}>
        <FontAwesomeIcon icon={faFilePdf} /> Ficha personal
    </button>                
</div>
}