import React from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUsers } from '@fortawesome/free-solid-svg-icons';
import { faCompass, faUser } from '@fortawesome/free-regular-svg-icons';

export default function Nav({ usuario, logout, cuatrimestreActivo }) {
  return (
    <nav className="Nav">
      <ul className="Nav__links">
        <li>
          <Link className="Nav__link razon-social" to="/">
            Escuela de Música Contemporánea
          </Link>
        </li>
        {usuario && cuatrimestreActivo && <li title="Cuatrimestre activo" className="Nav__link-push">{cuatrimestreActivo.nombre}</li>}
        {usuario && <LoginRoutes usuario={usuario} logout={logout} />}
      </ul>
    </nav>
  );
}

function LoginRoutes({ usuario, logout }) {
  return (
    <>
      <li title="Listado de cursos" className="Nav__link-push">
        <Link className="Nav__link" to="/cursos">
          <FontAwesomeIcon icon={faUsers} />
        </Link>
      </li>
{/* <li className="Nav__link-margin-left">
        <Link className="Nav__link" to="/explore">
          <FontAwesomeIcon icon={faCompass} />
        </Link>
      </li>
  */}
      <li className="Nav__link-margin-left">

      <button className="Perfil__boton-logout" onClick={logout}>
      Salir
        </button>
      </li>
    </>
  );
}
