import React from 'react';
import ReactDOM from 'react-dom';
import '../modal.css'

const estiloDefault = {width:'500px'}

const Modal = ({ isShowing, hide, children, titulo, estilo }) => isShowing ? ReactDOM.createPortal(
    <React.Fragment>
        <div className="modal-overlay" />
        <div className="modal-wrapper" aria-modal aria-hidden tabIndex={-1} role="dialog">
            <div className="modal" style={!estilo ? estiloDefault : estilo }>
                <div className="modal-header">
                <div className="px-6 py-4">
                        <div className="mb-2 titulo-cab-modal">{titulo ? titulo : ''}</div>
                        <p className="text-gray-700 text-base">
                            
                        </p>
                    </div> 
                    <button type="button" className="modal-close-button" data-dismiss="modal" aria-label="Close" onClick={hide}>
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                    {children}
            </div>
        </div>
    </React.Fragment>, document.body
) : null;

export default Modal;