function PreguntaTipoInscripcion ({cerrar,onchange, binddato, alumno, inscribir}){
    const tipos = [0,"Regular","Recursada","Recuperatorio","Libre","Invitado"]

    return (
        <div className="mr-4 ml-4">
            <span className="mb-2 inline-block-1">{`¿Cómo desea inscribir a ${alumno.nombre}?`}</span>
            <div className="flex f-col">
            <div className="flex f-row">
                <select onChange={onchange} value={binddato} className="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                    <option key="tipos1" value="1">{tipos[1]}</option>
                    <option key="tipos2" value="2">{tipos[2]}</option>
                    <option key="tipos3" value="3">{tipos[3]}</option>
                    <option key="tipos4"  value="4">{tipos[4]}</option>
                    <option key="tipos5" value="4">{tipos[5]}</option>
                </select>
                <button><FontAwesomeIcon 
                                icon={faWindowClose} 
                                onClick={cerrar}/>
                            </button>
                <button onClick={inscribir}>Inscribir como {tipos[binddato]}
                    <FontAwesomeIcon 
                        icon={faCheckCircle}/>
                </button>
            </div>                              
            </div>   
                      
        </div>
    )
}    

function createMarkup(codigo) { return {__html: codigo}; };

function Alertas({alertas}){

    if (alertas.length===0){
        return null
    }

    return(
        <div className="max-w-sm rounded overflow-hidden shadow-lg mb-6 ml-4 AlertaContainer contenedor-curso-grupal">
        <div className="px-6 py-4">
            <div className="mb-2">Alertas</div>
            <p className="text-gray-700 text-base">
                {alertas[0].mensaje}
            </p>
        </div>  
        <div className="px-6 py-4">
            <div className="flex f-col">
            {alertas.map(
               (item)=>{
                   return(
                    <span key={item.id_materia} className="inline-block bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 mr-2 ml-2    ">
                    {item.descripcion}        
                </span>
                   )
               } 
            )}
            </div>
        </div>
    </div>)}

