function verSiYaInscripto(id,vector){
    let resultado = true;

    console.log('el vector de alumnos del curso',vector)
    console.log('el id del alumno',id)

    const verificar = vector.findIndex(item=>item.id_alumno===id)

    if (verificar===-1){
        resultado=false;
    }

    return resultado;
}

